<template>
    <div class="editor">

        <div class="import-container" :class="[importIsStarted ? '--visible' : '']">
            <p>Paste JSON data here:</p>
            <textarea placeholder="Data goes here..." v-model="importData"></textarea>
            <button @click="applyImport">Import JSON</button>
            <button @click="cancelImport">Cancel</button>
        </div>

        <p>
            <template v-if="selectedComponent">
                {{ selectedComponent.slotId }} ({{ selectedComponent.$options.name }}) &nbsp;|&nbsp;
            </template>
            Breakpoint: {{ currentBreakpoint }}
            <button v-on:click="copyJson">Copy JSON</button>
            <button v-on:click="startImport">Import JSON</button>
            <select v-model="componentForceTarget">
                <option value="used">Used (default)</option>
                <option value="pattern">Pattern</option>
                <option value="override">Override</option>
            </select>
        </p>

        <div v-if="selectedComponent && selectedComponent.options" class="config-container">

            <table class="config-table">
                <tr>
                    <th>Property + Value</th>
                    <th>From pattern</th>
                    <th>Override</th>
                </tr>
                <tr v-for="(optionFormat, optionKey) in selectedComponent.options">
                    <td>
                        {{optionKey}}: {{ selectedComponent.getConfig(optionKey) }}
                    </td>
                    <td>
                        <button v-if="!selectedComponent.defaultConfig.hasOwnProperty(optionKey)" @click="setConfig(optionKey)">+</button>
                        <button v-else @click="deleteConfig(optionKey)">x</button>

                        <option-editor v-if="selectedComponent.defaultConfig.hasOwnProperty(optionKey)" v-model="selectedComponent.defaultConfig[optionKey]" @change="setConfig(optionKey, $event)" :format="optionFormat"></option-editor>
                    </td>
                    <td>
                        <button v-if="!selectedComponent.overrideConfig.hasOwnProperty(optionKey)" @click="setOverride(optionKey)">+</button>
                        <button v-else @click="deleteConfig(optionKey, true)">x</button>

                        <option-editor v-if="selectedComponent.overrideConfig.hasOwnProperty(optionKey)" v-model="selectedComponent.overrideConfig[optionKey]" @change="setOverride(optionKey, $event)" :format="optionFormat"></option-editor>
                    </td>
                </tr>
            </table>
        </div>

    </div>
</template>

<script>
    import OptionEditor from './OptionEditor';
    import { mapGetters } from 'vuex'

    import EventBus from '../EventBus';

    export default {
        name: "componentEditor",
        data() {
            return {
                componentForceTarget: 'override',
                importIsStarted: false,
                importData: ''
            }
        },
        components: {
            OptionEditor
        },
        methods: {
            deleteConfig(key, override) {
                this.setConfig(key, null, {
                    override: override,
                    delete: true
                });
            },
            setOverride(key, value) {
                this.setConfig(key, value, {
                    override: true
                })
            },
            setConfig(key, value, config = { override: false, delete: false }) {
                let data = {
                    [key]: value
                };

                // Set default value if needed
                if(config.delete !== true && value === undefined) {
                    if(['top', 'left'].includes(key)) {
                        data = {
                            top: this.selectedComponent.config.top,
                            left: this.selectedComponent.config.left
                        }
                    } else {
                        data[key] = this.selectedComponent.config[key];
                    }


                // Delete both top and left at the same time
                } else if(config.delete === true && ['top', 'left'].includes(key)) {
                    data = {
                        top: null,
                        left: null
                    }
                }

                // Force animate in
                this.selectedComponent.isTransitionedIn = true;

                this.$store.commit('setComponentData', {
                    sectionId: this.selectedComponent.section.data.sectionId,
                    patternId: this.selectedComponent.section.data.patternId,
                    slotId: this.selectedComponent.slotId,
                    deleteProperties: config.delete,
                    override: config.override,
                    data
                });

                this.$forceUpdate();
                this.selectedComponent.key++;
            },
            startImport() {
                this.importIsStarted = true;
            },
            applyImport() {
                this.importIsStarted = false;
                let converted;
                try {
                    converted = JSON.parse(this.importData);
                } catch(e) {}

                if(converted) {
                    this.$store.commit('setConfig', converted);
                    this.$store.dispatch('fetchSections');
                    this.importData = '';
                } else {
                    alert('Could not parse JSON');
                }
            },
            cancelImport() {
                this.importIsStarted = false;
            },
            copyJson() {
                const json = JSON.stringify(this.$store.getters.config);

                window.navigator.clipboard.writeText(json).then(function() {
                    console.log('Async: Copying to clipboard was successful!');
                }, function(err) {
                    console.error('Async: Could not copy text: ', err);
                });
            },
        },
        computed: {
            ...mapGetters([
                'breakpoints',
                'currentBreakpoint',
                'selectedComponent'
            ]),
        },
        mounted() {

            /*
            document.addEventListener('dblclick', e => {
                if(e.shiftKey && e.target.closest('.hiring-banner')) {
                    setTimeout(() => {
                        this.setOverride('inAnim', 'flyUp');
                        console.log('set')
                    })
                }
            })
            */


            this.$el.addEventListener('mousedown', e => {
                e.stopPropagation();
            }, true)

            document.addEventListener('mousedown', e => {
                this.$store.commit('setSelectedComponent', {component: null});
            });

            EventBus.$on('componentPositionUpdated', (data) => {
                let override = false;

                if(this.componentForceTarget === 'override') {
                    override = true;
                } else {
                    override = this.selectedComponent.getConfigSource('top') === 'override';
                }

                this.setConfig('top', parseInt(this.selectedComponent.$el.style.top), { override: override });
                this.setConfig('left', parseInt(this.selectedComponent.$el.style.left), { override: override });
            })
        }
    }
</script>

<style lang="scss" scoped>

    .editor {
        position: fixed;
        top: 5px;
        right: 5px;
        padding: 14px;
        z-index: 10;
        background: #FFF;
        border: 1px solid rgba(0, 0, 0, 0.3);
        border-radius: 4px;
        overflow: hidden;
        font-size: 12px;
    }

    .import-container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: none;
        background: #0000009e;
        padding: 100px;
        color: #FFF;
        font-size: 20px;

        p {
            margin: 0;
        }

        textarea {
            display: block;
            width: 600px;
            height: 100px;
            margin: 10px 0;
        }

        &.--visible {
            display: block;
        }
    }

    .config-table {
        border-collapse: collapse;
        th {
            font-weight: normal;
        }
        td {
            border: 1px solid #DDD;
            padding: 4px 6px;
        }
    }
</style>
