<template>
    <div :id="data.sectionId" class="section">
        <div class="min-height">
            <hero-image v-if="data.heroImage" :image="data.heroImage" :cut="data.teaser" :class="[getColour('colour1', 'text')]"></hero-image>

            <div class="content-container-overflow">
                <div class="content-container">
                    <div class="bg-pattern" :class="['cut-' + data.teaser, this.getColour('colour1', 'background')]">
                        <div class="pattern" ref="bgPattern" :style="{ backgroundImage: 'url('+ backgroundPatternPath +')' }"></div>
                    </div>

                    <div v-if="selectedComponent" class="grid-overlay"></div>

                    <div class="content-inner" :style="{ height: sectionHeight + 'px' }">

                        <big-text v-for="(title, index) in data.title"
                                  :key="index"
                                  :slot-id="'bigText' + (parseInt(index)+1)">
                            {{ title }}
                        </big-text>

                        <member v-for="(member, index) in data.members"
                                :key="member.uuid"
                                :slot-id="'member' + (parseInt(index)+1)"
                                :jobTitle="member.title"
                                :name="member.firstname + ' ' + member.lastname"
                                :image="member.imageUrl">
                        </member>

                        <member :slot-id="'member' + (data.members.length + 1)"
                                jobTitle="Dummy 0"
                                name="Dummy Employee"
                        ></member>
                        <member :slot-id="'member' + (data.members.length + 2)"
                                jobTitle="Dummy 1"
                                name="Dummy Employee"
                        ></member>
                        <member :slot-id="'member' + (data.members.length + 3)"
                                jobTitle="Dummy 2"
                                name="Dummy Employee"
                        ></member>
                        <member :slot-id="'member' + (data.members.length + 4)"
                                jobTitle="Dummy 3"
                                name="Dummy Employee"
                        ></member>
                        <member :slot-id="'member' + (data.members.length + 5)"
                                jobTitle="Dummy 4"
                                name="Dummy Employee"
                        ></member>

                        <paragraph slot-id="paragraph1">{{ data.description }}</paragraph>

                        <hiring-banner v-for="(vacancy, index) in data.vacancies" :key="vacancy.uuid" :slot-id="'hiringBanner' + (parseInt(index)+1)" :title="vacancy.title" :url="vacancy.url">{{ data.description }}</hiring-banner>

                        <slot></slot>
                    </div>
                </div>
            </div>
        </div>
        <div class="section-bottom" ref="sectionBottom"></div>
    </div>

</template>

<script>
    import { mapGetters } from 'vuex'

    import { TweenMax } from 'gsap';
    import ScrollMagic from 'scrollmagic';
    import 'animation.gsap'
    import 'debug.addIndicators'

    import sectionMixin from '../sectionMixin'

    import colourSchemes from '../store/colourSchemes';
    import colourHelper from '../helpers/colours';

    import SectionTeaser from './SectionTeaser';
    import HeroImage from './HeroImage';

    import HiringBanner from './HiringBanner';
    import Member from './Member'
    import BigText from './BigText';
    import Paragraph from './Paragraph';

    import { ReactiveProvideMixin } from 'vue-reactive-provide'

    export default {
        name: "employee-section",
        props: ['data'],
        mixins: [
            ReactiveProvideMixin({
                name: 'reactiveInjects',
                include: ['outTransitionHasStarted'],
            }),
            sectionMixin
        ],
        inject: ['reactiveInjects'],
        provide() {
            return {
                section: this,
            };
        },
        data() {
            return {
                sectionHeight: 0,
                colourSchemes: colourSchemes,
                outTransitionHasStarted: false
            }
        },
        components: {
            BigText,
            HiringBanner,
            Member,
            Paragraph,
            SectionTeaser,
            HeroImage
        },
        computed: {
            ...mapGetters(['selectedComponent']),
            backgroundPatternPath() {
                return '/patterns/' + encodeURI(this.data.backgroundPattern);
            }
        },
        methods: {
            getColour(colourName, classname) {
                const colour = this.colourSchemes[this.data.colourScheme][colourName];

                if(classname === 'text') {
                    return colourHelper.textClass(colour);
                } else if(classname === 'background') {
                    return colourHelper.backgroundClass(colour);
                } else if(classname === 'border') {
                    return colourHelper.borderClass(colour);
                } else {
                    return colour;
                }
            },
            calcSectionHeight() {
                let max = 0;

                for(const child of this.$children) {
                    if('slotId' in child) {
                        let top = parseInt(child.top);
                        if(top + child.$el.offsetHeight > max) {
                            max = top + child.$el.offsetHeight;
                        }
                    }
                }

                this.sectionHeight = max + window.innerHeight*.2;
            },
        },
        mounted() {

            if(this.data.disableSectionOutAnimations !== true) {
                const transformer = this.$el.querySelector('.content-inner');
                if(!transformer) {
                    return;
                }
                const endEl = this.$refs.sectionBottom;

                new ScrollMagic.Scene({
                    triggerHook: 1,
                    //offset: offset,
                    triggerElement: endEl,
                    duration: window.innerHeight,
                })
                //.addIndicators()
                    .addTo(this.reactiveInjects.scrollMagicController)
                    .setTween(transformer, {
                        //opacity: 0,
                        //x: 200,
                        y: window.innerHeight/1.6 + 'px',
                        //scale: 1.5,
                        //ease: Linear.easeNone
                        ease: Power1.easeIn
                    })
                    .on('enter', () => {
                        this.outTransitionHasStarted = true;
                    })
                    .on('leave', () => {
                        this.outTransitionHasStarted = false;
                    })
            }



            let timeout = null;
            window.addEventListener('resize', () => {
                clearTimeout(timeout);
                timeout = setTimeout(this.calcSectionHeight, 200)
            })
            //this.calcSectionHeight();
            setTimeout(this.calcSectionHeight, 50);



            let isDesktop = window.innerWidth > 1024;
            window.addEventListener('resize', () => {
                isDesktop = window.innerWidth > 1024;
            })
            let suspendAtScrollY = null;
            window.addEventListener('scroll', (e) => {
                if(!isDesktop) return;

                if(!suspendAtScrollY) suspendAtScrollY = window.scrollY;

                if(this.data.isInView) {
                    if(!this.$refs.bgPattern) {
                        console.log(this.$el.id);
                    }
                    const offset = (window.scrollY - suspendAtScrollY) * .4;
                    //console.log('transformY('+ offset +'px)');
                    this.$refs.bgPattern.style.transform = 'translateY('+ offset +'px)';
                    //this.$refs.bgPattern.style.backgroundPositionY = window.scrollY*.4 + 'px';
                }
            });

        }
    }
</script>

<style lang="scss" scoped>

    @import "../scss/section";

</style>
