                                startTrans();
                            }, 30)
                        }
                    })
                }
            };
            window.transitionComponentIn = (el, groupId) => {
                if(!transGroups.hasOwnProperty(groupId)) {
                    transGroups[groupId] = [];
                }
                transGroups[groupId].push(el);
                startTrans();
            };
            */

            // Staggered in-transitions
            let elsToTrans = [];
            const startTrans = () => {
                for(const eli in elsToTrans) {
                    const el = elsToTrans[eli];
                    if(el === null) continue;

                    setTimeout(() => {
                        el.classList.add('--transition-in');
                        elsToTrans[eli] = null;
                        startTrans();
                    }, 20)

                    return true;
                }
            };
            window.transitionComponentIn = (el) => {
                console.log(el);
                elsToTrans.push(el);
                startTrans();
            };





            /*
