                }
                lastScrollPos = window.scrollY;


            })

            const members = document.querySelectorAll('.member');
            let els = [];
            for(const member of members) {
                els.push({
                    root: member,
                    transformer: member.querySelector('.transformer')
                })
            }

            setInterval(() => {
                for(const el of els) {
                    if(el.root.dataset.inView) {
                        if(el.transformer.dataset.transY < 300) { continue };
                        console.log('in view!', el);
                        const newTrans = el.transformer.dataset.transY ? el.transformer.dataset.transY - 1 : 0;
                        el.transformer.style.transform = 'transformY('+ newTrans +'px)';
                        el.transformer.dataset.transY = newTrans;
                    }
                }
            }, 30);
            */







            const lazyObserver = lozad(); // lazy loads elements with default selector as '.lozad'
            lazyObserver.observe();


            this.calcBreakpoint();
            window.addEventListener('resize', this.calcBreakpoint); // Todo: Throttle

            //AOS.init();
            setTimeout(AOS.init, 500);

            const debAOS = _.debounce(AOS.refresh, 200);
            window.addEventListener('resize', debAOS);
