<template>
    <div class="option-editor">
        <input @input="emit" v-if="format === 'number'" type="number" :value="value">
        <input @input="emit" v-if="format === 'px'" type="text" :value="value">

        <select @change="emit" v-if="typeof format === 'object'">
            <option v-for="(formatName,formatValue) in format" :value="formatValue" :selected="formatValue === value">{{ formatName }}</option>
        </select>
    </div>
</template>

<script>
    export default {
        name: 'OptionEditor',
        props: {
            format: {
                required: true,
            },
            value: {}
        },
        methods: {
            emit(e) {
                this.$emit('change', e.target.value);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .option-editor {
        display: inline;
    }

    input[type=number] {
        width: 36px;
    }
</style>
