import Vue from 'vue';
import Vuex from 'vuex';

import config from './config';

import apiData from './apiMock';
import sectionData from './sectionData';

import * as constants from '../constants';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {

        // Desktop first
        breakpoints: [
            -1,     // 1300 in width
            1340,   // 900px in width
            940,    // 600px in width
            620     // 320 in width
        ],
        currentBreakpoint: -1,

        config: config,

        // For ComponentEditor
        selectedComponent: null,

        sections: null

    },
    getters: {
        getVisibleSection: state => {
            return state.visibleSection;
        },
        screenIsMax: state => breakpoint =>  {
            //console.log(breakpoint, state.currentBreakpoint)
            if(breakpoint === constants.BREAKPOINT_DESKTOP) {
                return true;
            } else if(breakpoint <= state.currentBreakpoint) {
                return true;
            } else {
                return false;
            }
        },
        sections: state => {
            return state.sections;
        },
        componentOverrideData: state => data => {
            try {
                let breakpoints = state.config.overrides[data.sectionId].slots[data.slotId].breakpoints;
                if(breakpoints.hasOwnProperty(state.currentBreakpoint)) {
                    return breakpoints[state.currentBreakpoint];
                } else {
                    return false;
                }
            } catch(e) {
                return false;
            }
        },
        componentData: (state) => data => {
            let ret = false;

            let slots = state.config.patterns[data.patternId].slots;

            if(!slots.hasOwnProperty(data.slotId)) {
                return false;
            } else if(!slots[data.slotId].breakpoints.hasOwnProperty(state.currentBreakpoint)) {
                return false;
            }

            if(slots[data.slotId].breakpoints.hasOwnProperty(state.currentBreakpoint)) {
                ret = slots[data.slotId].breakpoints[state.currentBreakpoint];
            } else {
                ret = slots[data.slotId].breakpoints[-1];
            }

            /*
            if(data.type === 'merged') {
                const overrideData = getters.componentOverrideData(data);

                if(overrideData !== false) {
                    return {...ret, ...overrideData};
                }
            }
            */

            return ret;
        },
        selectedComponent: state => {
            return state.selectedComponent;
        },
        config: state => {
            return state.config;
        },
        breakpoints: state => {
            return state.breakpoints
        },
        currentBreakpoint: state => {
            return state.currentBreakpoint;
        }
    },
    actions: {
        fetchSections({ commit }) {
            let tmpSections = [];
            let counter = 0;

            for(const teamIndex in apiData) {
                const apiTeam = apiData[teamIndex];
                let localTeam;

                for(let local of sectionData) {
                    if(local.belongsTo === apiTeam.uuid) {
                        localTeam = local;
                        break;
                    }
                }

                if(!localTeam) continue;

                if(localTeam.hasOwnProperty('ignore')) continue;

                let tmp = localTeam;

                tmp.members = apiTeam.staffs.map(employee => {
                    // Hack for breaking single long words (with non visible space)
                    employee.title = employee.title.replace('Anwendungsentwicklung', 'Anwendungs&#8203;entwicklung')
                                                    .replace('Kommunikationsreferent', 'Kommunikations&#8203;referent')
                    return employee;
                });
                tmp.vacancies = apiTeam.vacancies;
                tmp.position = apiTeam.position;
                tmp.isInView = false;
                tmp.hasMajorityScreenEstate = false;
                counter++;

                // Merge teams if neccessary (medien + verlag)
                if(localTeam.hasOwnProperty('include')) {
                    for(let team of apiData) {
                        if(team.uuid === localTeam.include) {
                            tmp.members = [...tmp.members, ...team.staffs];
                            break;
                        }
                    }
                }

                tmpSections.push(tmp);
            }

            tmpSections.sort((a, b) => a.position - b.position);

            commit('setSections', {data: tmpSections});
        }
    },
    mutations: {
        updateVisibleSection(state) {

            let highest;
            for(const sectIndex in state.sections) {
                const sect = state.sections[sectIndex];

                state.sections[sectIndex].hasMajorityScreenEstate = false;

                if(sect.intersectionRatio > 0) {
                    const el = document.querySelector('#' + sect.sectionId);
                    const visiblePixels = el.clientHeight*sect.intersectionRatio;

                    if(!highest || highest.visiblePixels < visiblePixels) {
                        highest = {
                            sectionIndex: sectIndex,
                            visiblePixels: visiblePixels
                        }
                    }

                }
            }

            if(highest) {
                state.sections[highest.sectionIndex].hasMajorityScreenEstate = true;
            }
        },
        setConfig(state, newConfig) {
            state.config = newConfig;
        },
        setSections(state, sections) {
            state.sections = sections.data;
        },
        setComponentData(state, data) {
            //console.log('saving:', data)

            const breakpoint = data.breakpoint || state.currentBreakpoint;

            if(data.override) {

                let overrides = state.config.overrides;

                // Create section if not there
                if(!overrides.hasOwnProperty(data.sectionId)) {
                    overrides[data.sectionId] = {
                        slots: {}
                    }
                }

                // Create slot if not there
                if (!overrides[data.sectionId].slots.hasOwnProperty(data.slotId)){
                    overrides[data.sectionId].slots[data.slotId] = {
                        breakpoints: {}
                    }
                }

                // Create breakpoint if not there
                if (!overrides[data.sectionId].slots[data.slotId].breakpoints.hasOwnProperty(breakpoint)){
                    overrides[data.sectionId].slots[data.slotId].breakpoints[breakpoint] = {}
                }

                for(const configProperty in data.data) {
                    if(data.deleteProperties) {
                        delete overrides[data.sectionId].slots[data.slotId].breakpoints[breakpoint][configProperty];
                    } else {
                        overrides[data.sectionId].slots[data.slotId].breakpoints[breakpoint][configProperty] = data.data[configProperty];
                    }
                }

                state.config.overrides = overrides;

            } else {
                let slots = state.config.patterns[data.patternId].slots;

                if(!slots.hasOwnProperty(data.slotId)) {
                    slots[data.slotId] = {
                        breakpoints: {}
                    }
                }
                if(!slots[data.slotId].breakpoints.hasOwnProperty(breakpoint)) {
                    slots[data.slotId].breakpoints[breakpoint] = {};
                }

                for(const configProperty in data.data) {
                    if(data.deleteProperties) {
                        delete slots[data.slotId].breakpoints[breakpoint][configProperty];
                    } else {
                        slots[data.slotId].breakpoints[breakpoint][configProperty] = data.data[configProperty];
                    }
                }

                state.config.patterns[data.patternId].slots = slots;
            }
        },
        setSelectedComponent(state, data) {
            state.selectedComponent = data.component;
        },
        setCurrentBreakpoint(state, breakpoint) {
            state.currentBreakpoint = breakpoint;
        },
    }
})
