                }
            }
        },
        inject: ['reactiveInjects'],
        mixins: [mixin],
        mounted() {

            if(this.reactiveInjects.scrollMagicController) {
                const transformer = this.$el.querySelector('.transformer');
                if(!transformer) { return; }

                const endEl = this.$refs.sectionBottom;


                new ScrollMagic.Scene({
                    triggerHook: 1,
                    triggerElement: endEl,
                    duration: window.innerHeight,
                })
                //.addIndicators()
                    .addTo(this.reactiveInjects.scrollMagicController)
                    .setTween(this.$refs.startBg, {
                        scale: 1.3,
                        ease: Linear.easeNone
                    })


